function ViewportQuestion({ questions, searchValue }) {
  return (
    <div className="container">
      {questions.seed_39.map((listMap) => {
        let { question, select, answer, visible } = listMap;
        select.map((tmp) => {
          return (visible = tmp.indexOf(searchValue) !== -1 ? true : visible);
        });
        return (
          <div className={visible ? "problem visible" : "problem unvisible"}>
            <div className="question">{question}</div>
            <div className="select">
              {select.map((value, index) => {
                return (
                  <div className={index + 1 === answer ? "answer" : "none"}>
                    {value}
                  </div>
                );
              })}
            </div>
          </div>
        );
      })}
    </div>
  );
}

export default ViewportQuestion;
