import React, { useState } from "react";
import "./css/search.scss";
import ViewportQuestion from "./Questions/ViewportQuestion";
function SearchForm({ questions }) {
  const [inputs, setInputs] = useState({ SearchValue: "" });
  const { SearchValue } = inputs;
  const onChange = (e) => {
    const { name, value } = e.target;
    setInputs({
      ...inputs,
      [name]: value,
    });
  };
  return (
    <div className="container">
      <input
        name="SearchValue"
        placeholder=""
        onChange={onChange}
        value={SearchValue}
        autofocus
      />
      <ViewportQuestion questions={questions} searchValue={SearchValue} />
    </div>
  );
}

export default SearchForm;
